<template>
  <Container :title="title">
    <v-form @submit="submit">
      <template slot="base">
        <group v-if="!guestbook" :label="lang.arc_title"
               type="input" :disabled="true" v-model="comment.title" />
        <group v-if="guestbook" :label="lang.username" type="input"
               :disabled="true" v-model="comment.username"/>
        <group :label="lang.email" type="input" :disabled="true" v-model="comment.email" />
        <group v-if="!guestbook" :label="lang.comment_user" type="input"
               :disabled="true" v-model="comment.username" />
        <group v-if="!guestbook" :label="lang.reply_target" type="input"
               :disabled="true" v-model="comment.reply_target" />
        <group :label="lang.comment_content" type="textarea" v-model="comment.content" />
        <group :label="lang.reply_content" type="textarea" v-model="comment.reply_content" />
        <group :label="lang.status" type="status" v-model="comment.status" />

      </template>
    </v-form>
  </Container>
</template>

<script>
  import {Container,VForm,Group} from "@/components"
  export default {
    components: { Group, VForm,  Container },
    data(){
      return {
        guestbook: false,
        lang:this.$t('comment'),
      }
    },
    computed:{
      title(){
        return this.guestbook? this.$t('comment.guestbookTitle'):this.$t('comment.editTitle')
      },
      comment:{
        set(value){
          this.$store.commit('comment/SET_COMMENT', value)
        },
        get(){
          return this.$store.state.comment.comment
        }
      }
    },
    created(){
      const uuid = this.$route.query['id']
      this.guestbook = this.$route.query['guestbook']
      this.$store.dispatch('comment/fetchData',uuid)
    },
    methods:{
      submit(){
        const comment = this.comment
        const postData = {
          reply_id: comment.id,
          reply_type: comment.type || 0,
          reply_username: this.$cookies.get('username'),
          reply_content: comment.reply_content || "",
          id: comment.id,
          content: comment.content,
          status: comment.status
        }
        if (postData.content === undefined || postData.content.length === 0){
          this.$toast.warn({
            title:'表单验证失败~',
            message: '请输入需要回复的消息~'
          })
          return
        }
        this.$store.dispatch('comment/editModel', postData).then( resp =>{
          if (resp){
            this.$toast.success({
              title:'API 请求成功',
              message: resp.error
            })
          }
        })
      }
    }
  }
</script>



